import React from "react";
import classes from '../styles/Price.module.css';
import { motion } from "framer-motion";


const Price = () => {
    const prices = [
        {
            id: 1,
            title: 'Дневной стационар (1 час)',
            price: '440'
        },
        {
            id: 2,
            title: 'Палата двухместная (сутки)',
            price: '4400'
        },
        {
            id: 3,
            title: 'Палата интенсивной терапии (сутки)',
            price: '7700'
        },
        {
            id: 4,
            title: 'Палата одноместная (сутки)',
            price: '6600'
        },
    ];

    const blocks = window.screen.availWidth > 425 ? {
        hidden: {
            x: -100,
            opacity: 0
        },
        show: {
            x: 0,
            opacity: 1,
            transition: {
                duration: 1
            }
        }
    }
    :
    {
        hidden: {
            y: -100,
            opacity: 0
        },
        show: {
            y: 0,
            opacity: 1,
            transition: {
                duration: 1
            }
        }
    }


    return (
        <section className={classes.price}>
            <div className={classes.container}>
                <h1 className={classes.priceHeading}>
                    Цены на услуги госпитализации
                </h1>
                <div className={classes.priceItems}>
                    {prices.map((e) => <motion.div className={classes.priceItem} key={e.id} whileHover={{ scale: 1.02 }} initial={blocks.hidden} whileInView={blocks.show} viewport={{once: true}}>
                        <h3 className={classes.itemHeading}>{e.title}</h3>
                        <p className={classes.itemPrice}>{e.price} руб.</p>
                    </motion.div>)}
                </div>
            </div>
        </section>
    )
}

export default Price;