import React, { useState } from "react";
import classes from '../styles/About.module.css';
import { motion, useAnimation } from "framer-motion";

// Images
import computer from "../images/computer.svg";
import computerW from "../images/computerW.svg";
import mic from '../images/microscope.svg';
import micW from '../images/microscopeW.svg';
import money from '../images/money.svg';
import moneyW from '../images/moneyW.svg';


const About = () => {
    const points = [
        {
            title: 'Современное оборудование',
            description: 'Автомобили службы, предназначенные для экстренной коммерческой госпитализации, оборудованы современными оборудованием и приборами, позволяющими поддерживать состояние больного и при необходимости оказывать необходимую помощь.',
        },
        {
            title: 'Профессионализм',
            description: 'В состав бригад скорой помощи входят высококвалифицированные специалисты, готовые провести полный комплекс неотложных мероприятий в любых ситуациях: от помощи больным онкологией до платной госпитализации ребенка или пожилого человека. В нашей медицинской службе работают профессионалы с большим опытом.',
        },
        {
            title: 'Соотношение цена–качество',
            description: 'Стоимость госпитализации медицинской службой – средняя по Москве. При этом профессионализм наших специалистов и наличие новейшего оборудования позволяют спасать десятки жизней.',
        }
    ];
    const [point, setPoint] = useState(0);
    const [isPc, setIsPc] = useState(true);
    const [isMic, setIsMic] = useState(false);
    const [isMoney, setIsMoney] = useState(false);

    const setPc = async () => {
        setIsPc(true);
        setIsMic(false);
        setIsMoney(false);
        await controls.start('hidden');
        await bg1.start('show');
        bg3.start('hidden');
        bg4.start('hidden');
        setPoint(0);
        await controls.start('show');
    }

    const setMic = async () => {
        setIsPc(false);
        setIsMic(true);
        setIsMoney(false);
        await controls.start('hidden');
        bg1.start('hidden');
        await bg3.start('show');
        bg4.start('hidden');
        setPoint(1);
        await controls.start('show');
    }

    const setMoney = async () => {
        setIsPc(false);
        setIsMic(false);
        setIsMoney(true);
        await controls.start('hidden');
        bg1.start('hidden');
        bg3.start('hidden');
        await bg4.start('show');
        setPoint(2);
        await controls.start('show');
    }

    const headings = window.screen.availWidth > 425 ? {
        hidden: {
            x: -100,
            opacity: 0
        },
        show: {
            x: 0,
            opacity: 1,
            transition: {
                duration: 1
            }
        }
    }
    :
    {
        hidden: {
            y: -100,
            opacity: 0
        },
        show: {
            y: 0,
            opacity: 1,
            transition: {
                duration: 1
            }
        }
    }

    const blocks = window.screen.availWidth > 425 ? {
        hidden: {
            x: 100,
            opacity: 0
        },
        show: {
            x: 0,
            opacity: 1,
            transition: {
                duration: 1
            }
        }
    }
    :
    {
        hidden: {
            y: 100,
            opacity: 0
        },
        show: {
            y: 0,
            opacity: 1,
            transition: {
                duration: 1
            }
        }
    }

    const bg = {
        hidden: {
            opacity: 0
        },
        show: {
            opacity: 1,
            transition: {
                duration: 0.5
            }
        }
    };

    const controls = useAnimation();
    const bg1 = useAnimation();
    const bg3 = useAnimation();
    const bg4 = useAnimation();


    return (
        <section className={classes.about}>
            <div className={classes.wraper}>
                <motion.div className={classes.backgroundImage1} initial={bg.show} animate={bg1} variants={bg}></motion.div>
                <motion.div className={classes.backgroundImage3} initial={bg.hidden} animate={bg3} variants={bg}></motion.div>
                <motion.div className={classes.backgroundImage4} initial={bg.hidden} animate={bg4} variants={bg}></motion.div>
                <div className={classes.container}>
                    <motion.div className={classes.aboutText} animate={controls} variants={headings} initial={headings.hidden} whileInView={headings.show} viewport={{once: true}}>
                        <h1 className={classes.aboutHeading}>
                            {points[point].title}
                        </h1>
                        <p className={classes.aboutP}>
                            {points[point].description}
                        </p>
                    </motion.div>
                    <motion.div className={classes.aboutBtns} initial={blocks.hidden} whileInView={blocks.show} viewport={{once: true}}>
                        <motion.div className={`${classes.aboutBtn} ${classes.pc} ${isPc ? classes.active : ''}`} onClick={setPc} whileHover={{ scale: 1.1 }} whileTap={{ scale: 0.9 }}>
                            <img src={isPc ? computerW : computer} alt="" className={classes.btnImg} />
                        </motion.div>
                        <div className={classes.btnsContainer}>
                            <motion.div className={`${classes.aboutBtn} ${classes.mic} ${isMic ? classes.active : ''}`} onClick={setMic} whileHover={{ scale: 1.1 }} whileTap={{ scale: 0.9 }}>
                                <img src={isMic ? micW : mic} alt="" className={classes.btnImg} />
                            </motion.div>
                            <motion.div className={`${classes.aboutBtn} ${classes.money} ${isMoney ? classes.active : ''}`} onClick={setMoney} whileHover={{ scale: 1.1 }} whileTap={{ scale: 0.9 }}>
                                <img src={isMoney ? moneyW : money} alt="" className={classes.btnImg} />
                            </motion.div>
                        </div>
                        
                    </motion.div>
                </div>
            </div>
        </section>
    )
};

export default About;
