import React from "react";
import { motion } from "framer-motion";
import classes from '../styles/Main.module.css';


import hours from '../images/24.svg';
import building from '../images/building.svg';
import people from '../images/people.svg';


const Main = () => {
    const headings = window.screen.availWidth > 425 ? {
        hidden: {
            x: -100,
            opacity: 0
        },
        show: {
            x: 0,
            opacity: 1,
            transition: {
                duration: 1
            }
        }
    }
    :
    {
        hidden: {
            y: -100,
            opacity: 0
        },
        show: {
            y: 0,
            opacity: 1,
            transition: {
                duration: 1
            }
        }
    }


    const blocks = window.screen.availWidth > 425 ? {
        hidden: {
            x: 100,
            opacity: 0
        },
        show: {
            x: 0,
            opacity: 1,
            transition: {
                duration: 1
            }
        }
    }
    : {
        hidden: {
            y: 100,
            opacity: 0
        },
        show: {
            y: 0,
            opacity: 1,
            transition: {
                duration: 1
            }
        }
    }


    return (
        <main className={classes.main}>
            <div className={classes.wraper}>
                <div className={classes.container}>
                    <div className={classes.mainText}>
                        <motion.h1 className={classes.mainTextHeading} initial={headings.hidden} whileInView={headings.show} viewport={{once: true}}>
                            Госпитализация
                        </motion.h1>
                        <motion.p className={classes.mainTextP} initial={headings.hidden} whileInView={headings.show} viewport={{once: true}}>
                            Оказание высококвалифицированной, профессиональной медицинской помощи
                        </motion.p>
                    </div>
                    <motion.div className={classes.mainBlocks} initial={blocks.hidden} whileInView={blocks.show} viewport={{once: true}}>
                        <motion.div className={classes.mainBlock} whileHover={{ scale: 1.1 }}>
                            <img src={hours} alt="" className={classes.blockImg} />
                            <p className={classes.blockText}>
                                Круглосуточно и без выходных
                            </p>
                        </motion.div>
                        <motion.div className={classes.mainBlock} whileHover={{ scale: 1.1 }}>
                            <img src={building} alt="" className={classes.blockImg} />
                            <p className={classes.blockText}>
                                Есть своя поликлиника
                            </p>
                        </motion.div>
                        <motion.div className={classes.mainBlock} whileHover={{ scale: 1.1 }}>
                            <img src={people} alt="" className={classes.blockImg} />
                            <p className={classes.blockText}>
                                Скорая помощь для взрослых
                            </p>
                        </motion.div>
                    </motion.div>
                </div>
            </div>
        </main>
    )
}

export default Main